                                                                    <strong>Conta: </strong> {{r.conta.codigo}} - {{r.conta.nome}}
                                                                </span>
                                                            </span>
                                                        </p>

                                                        
                                                    </td>
                                                </tr>
                                            </tbody>
                                            <tfoot>
                                                <tr>
                                                    <td colspan="8"> 
                                                        <paginacao-componente :source="paginacaoLancamentos" @navigate="navegarLancamentos($event)"></paginacao-componente>
                                                    </td>
                                                </tr>
                                            </tfoot>
                                        </table>
                                    </div>
                                </div>

                                <form @submit.prevent="salvarComissoes()" class="tab-pane fade" id="tab-comissoes" role="tabpanel" aria-labelledby="tab-comissoes-tab">
                                    <div class="row">
                                        <div class="col-sm-3">
                                            <!-- <div class="float-right">
                                                <div class="dropdown">
                                                    <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"> <span class="d-none d-sm-inline-block">Ações <i class="mdi mdi-chevron-down"></i></span></button>
                                                    <div class="dropdown-menu dropdown-menu-right dropdown-menu-md" style="">
                                                        <a class="dropdown-item" :href="linkImprimirExtrato()" target="_blank">
                                                            <i class="mdi mdi-printer mr-1"></i> Imprimir Extrato
                                                        </a>

                                                        <!-- <a class="dropdown-item" href="imprimir('entrega')" target="_blank">
                                                            <i class="mdi mdi-printer mr-1"></i> Imprimir Extrato
                                                        </a> 
                                                    </div>
                                                </div>
                                            </div> -->
                                        </div>
                                    </div>
                                        
                                    <div class="row">
                                        <div class="col-sm-12">
                                            <div class="table-responsive">
                                                <table class="table mb-0" >
                                                    <thead>
                                                        <tr>
                                                            <th>Grupo</th>
                                                            <th>Subgrupo</th>
                                                            <th style="max-with: 175px">Comissão</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody v-for="(registro,index) in subgrupos" :key="index">
                                                        <tr >
                                                            <td>{{registro.grupo.id}} - {{registro.grupo.descricao}}</td>
                                                            <td>{{registro.id}} - {{registro.descricao}} </td>
                                                            <td>
                                                                <money 
                                                                    class="form-control"
                                                                    v-model.lazy="registro.pivot.comissao"
                                                                    v-bind="percentual"
                                                                ></money>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                    
                                                </table>
                                            </div>
                                        </div>
                                         <div class="col-md-12 text-center" v-if="errosComissoesSubGrupos || sucessoComissoesSubGrupo">
                                            <mensagem-componente v-if="errosComissoesSubGrupos" :erros="errosComissoesSubGrupos" @fechar="errosComissoesSubGrupos = null"></mensagem-componente>
                                            <mensagem-componente v-if="sucessoComissoesSubGrupo" :sucesso="sucessoComissoesSubGrupo" @fechar="sucessoComissoesSubGrupo = null"></mensagem-componente>
                                        </div>
                                        <div class="col-md-12 button-items text-center">

                                            <!-- <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'parceirosLista'}">
                                                <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                                            </router-link> -->

                                            <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                                                <i class="bx bxs-save d-block font-size-16"></i> Atualizar Comissões
                                            </button>

                                        </div>
                                    </div>
                                </form>

                                <form @submit.prevent="salvarTabelaServicos()" class="tab-pane fade" id="tab-servicos" role="tabpanel" aria-labelledby="tab-servicos-tab">
                                    <div class="row">
                                        <div class="col-sm-3">
                                            <!-- <div class="float-right">
                                                <div class="dropdown">
                                                    <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"> <span class="d-none d-sm-inline-block">Ações <i class="mdi mdi-chevron-down"></i></span></button>
                                                    <div class="dropdown-menu dropdown-menu-right dropdown-menu-md" style="">
                                                        <a class="dropdown-item" :href="linkImprimirExtrato()" target="_blank">
                                                            <i class="mdi mdi-printer mr-1"></i> Imprimir Extrato
                                                        </a>

                                                        <!-- <a class="dropdown-item" href="imprimir('entrega')" target="_blank">
                                                            <i class="mdi mdi-printer mr-1"></i> Imprimir Extrato
                                                        </a> 
                                                    </div>
                                                </div>
                                            </div> -->
                                        </div>
                                    </div>
                                        
                                    <div class="row">
                                        <div class="col-sm-12">
                                            <div class="table-responsive">
                                                <h4>Serviços</h4>
                                                <table class="table mb-0" >
                                                    <thead>
                                                        <tr>
                                                            <th>Serviço</th>
                                                            <th>Unidade</th>
                                                            <th style="max-with: 175px">Valor Padrão</th>
                                                            <th style="max-with: 175px">Valor Unitário</th>
                                                            <th>#</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody v-for="(registro,index) in servicos" :key="index">
                                                        <tr >
                                                            <td>{{registro.id}} - {{registro.nome}}</td>
                                                            <td class="text-center">{{registro.unidade_medida}} </td>
                                                            <td>
                                                                <money 
                                                                    :disabled="true"
                                                                    class="form-control"
                                                                    v-model.lazy="registro.valor_unitario"
                                                                    v-bind="preco"
                                                                ></money>
                                                            </td>
                                                            <td>
                                                                <money 
                                                                    class="form-control"
                                                                    v-model.lazy="registro.pivot.valor_unitario"
                                                                    v-bind="preco"
                                                                ></money>
                                                            </td>
                                                            <th>{{registro.ativo ? 'Ativo': 'Inativo'}}</th>
                                                        </tr>
                                                    </tbody>
                                                    
                                                </table>
                                            </div>
                                        </div>
                                         <div class="col-md-12 text-center" v-if="errosTabelaServicos || sucessoTabelaServicos">
                                            <mensagem-componente v-if="errosTabelaServicos" :erros="errosTabelaServicos" @fechar="errosTabelaServicos = null"></mensagem-componente>
                                            <mensagem-componente v-if="sucessoTabelaServicos" :sucesso="sucessoTabelaServicos" @fechar="sucessoTabelaServicos = null"></mensagem-componente>
                                        </div>
                                        <div class="col-md-12 button-items text-center">

                                            <!-- <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'parceirosLista'}">
                                                <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                                            </router-link> -->

                                            <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                                                <i class="bx bxs-save d-block font-size-16"></i> Atualizar Tabela de Preços
                                            </button>

                                            <button type="button" class="btn btn-primary waves-effect waves-light w-sm" title="Imprimir" @click="imprimirTabela()">
                                                <i class="bx bx bx-printer d-block font-size-16"></i> Imprimir Tabela de Preços
                                            </button>                                            

                                        </div>
                                    </div>
                                </form>

                                
                            
                            </div>
                            
                        </div>
                    </div>
                    
                </div>
            </div>
            <log-componente v-if="form.id" :index="form.id" subject_type="App\Models\Cliente\Parceiro"  :subject_id="form.id" ></log-componente>
        </div>
    </div>
</template>

<script>

import { required, email, maxLength, requiredIf} from "vuelidate/lib/validators";
import { ParceiroService, EnderecoService, CaixaService, ServicoItemService } from '../../../services'
import moment from 'moment'

const parceiroService = new ParceiroService();
const enderecoService = new EnderecoService();
const caixaService = new CaixaService();
const servicoItemService = new ServicoItemService();

export default {

  data() {
        return {
            form: {
                id: null,
                apelido: '',
                estrangeiro: false,
                razao_social: '',
                nome_fantasia: '',

                tipo_pessoa: 'FISICA',
                tipo_documento: '',
                documento: '',
                rg_ie_estado: '',
                rg_ie: '',
                im: '',
                suframa: '',
                cliente: true,  
                fornecedor: true,
                recorrente: false,
                ativo: true,
                cidade_id: '',
                endereco_tipo: '',
                logradouro: '',
                numero: '',
                complemento: '',
                bairro: '',
                cep: '',
                fone: '',
                email: '',
                           
            },

            lancamentos: [],
            saldos: [],
            
            subgrupos: [],
            sucessoComissoesSubGrupo: null,
            errosComissoesSubGrupos: null,

            saldosCaixa: [],
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
            
            timer: null,
            cidades: [],
            cidade:{},
            buscandoCidade: false,

            filtroLancamentos:{
                ordenar: 'data_lancamento',
                ordem: 'desc',
                page: 1,
                per_page: 50,
                texto: '',
                data_inicial: moment('01-01-2020', 'DD-MM-YYYY').toDate(),
                data_final: moment().toDate(),
            },
            paginacaoLancamentos: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 50
            },


            sucessoTabelaServicos: null,
            errosTabelaServicos : null,
            servicos: []
        } ;
    },
    validations() {
        return {
            form: {
                apelido: { required, maxLength:maxLength(50)},
                razao_social: { required, maxLength:maxLength(200)},
                nome_fantasia: { required: requiredIf(function(){ return this.form.tipo_pessoa == 'JURIDICA' && !this.form.estrangeiro }), maxLength:maxLength(200)},
                tipo_pessoa: { required, maxLength:maxLength(20)},
                tipo_documento: {maxLength:maxLength(20)}, //{ required: requiredIf(function(){ return this.form.estrangeiro }), maxLength:maxLength(20)},
                documento: {maxLength:maxLength(20)}, //{ required, maxLength:maxLength(20), validarCpfCnpj: validarCpfCnpj(this.form.tipo_pessoa, this.form.estrangeiro)},
                rg_ie: { maxLength:maxLength(20)},
                rg_ie_estado: { maxLength:maxLength(2)},  //required: requiredIf(function(){ return this.form.tipo_pessoa == 'FISICA' && this.form.rg_ie.length > 0 && !this.form.estrangeiro }), maxLength:maxLength(2)},
                im: { maxLength:maxLength(20)},
                suframa: { maxLength:maxLength(20)},                
                ativo: { required },
                cliente: { required },
                fornecedor: { required },
                cidade_id: { },
                endereco_tipo: { maxLength:maxLength(20)},
                logradouro: { maxLength:maxLength(100)},
                numero: { maxLength:maxLength(10)},
                complemento: { maxLength:maxLength(100)},
                bairro: { maxLength:maxLength(100)},
                cep: {  maxLength:maxLength(10)}, //required: requiredIf(function(){ return !this.form.estrangeiro }),
                fone: { maxLength:maxLength(20)},
                email: { email, maxLength:maxLength(150)},
            },
        }
        

    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }       
    },
    methods: {

        submit: function(){

            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            if(!obj.estrangeiro){
                obj.tipo_documento = obj.tipo_pessoa == 'JURIDICA' ? 'CNPJ' : 'CPF';
            }
            let request = null;
            if(obj.id)    
                request = parceiroService.atualizar(obj);
            else
                request = parceiroService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            parceiroService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    if(response.data.cidade)
                        this.cidade = { id: response.data.cidade.id, nome: response.data.cidade.nome, sigla: response.data.cidade.estado.sigla };
                }).catch( erro => this.erros = erro)
        },

        buscarCep: function(){
            enderecoService.buscarEnderecoPorCep(this.form.cep)
                .then((response) => {
                if(response.data.logradouro){
                    this.form.logradouro = response.data.logradouro.substr( response.data.logradouro.indexOf(' ')+1 );
                    this.form.endereco_tipo = response.data.logradouro.substr(0, response.data.logradouro.indexOf(' '));
                }else{
                    this.form.logradouro = '';
                    this.form.endereco_tipo = '';
                }
                
                this.form.bairro = response.data.bairro;
                enderecoService.buscarCidadePorIbge(response.data.ibge)
                    .then((responseCidade) => {
                        
                        this.cidade = { id: responseCidade.data.id, nome:responseCidade.data.nome ,sigla: responseCidade.data.estado.sigla };
                        this.form.cidade_id = this.cidade.id;
                        this.cidades = [];
                    })
                    .catch((errorCity) => {
                        console.log(errorCity);
                    });
                })
                .catch((error) => {});
        },

        buscarCidadesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarCidades(text, loading)
            }, 700);
        },

        buscarCidades: function(text, loading){
            enderecoService.listarCidadesBusca({texto: text})
                .then( response =>  this.cidades = response.data.data )
                .catch( error => console.log(error))
        },

        buscarMatrizesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarMatrizes(text, loading)
            }, 700);
        },

        buscarMatrizes: function(text, loading){
            this.buscandoMatrizes = true;
            parceiroService.listar({texto: text, per_page: 50, id: this.form.id, matrizes: 1})
                .then( response =>  this.matrizes = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoMatrizes = false)
        },


        buscarLancamentos: function(){

        },

        buscarSaldo: function(){
            caixaService.buscarSaldoParceiro(this.form.id).then(response => this.saldos = response.data).catch(error => {
                Vue.$toast.error(`Não foi possível carregar o saldo do parceiro`);
                console.log("Erro ao buscar saldo do parceiro", error, error.response);
                setTimeout(() => {
                    this.buscarSaldo();
                }, 2000);
            })
            if(this.cc_parceiro)
                setTimeout(() => {
                    this.buscarSaldoCaixa()
                }, 200);
        },

        buscarSaldoCaixa: function(){
            caixaService.buscarSaldoCaixaParceiro(this.form.id).then(response => this.saldosCaixa = response.data).catch(error => {
                Vue.$toast.error(`Não foi possível carregar o saldo dos caixas do parceiro`);
                console.log("Erro ao buscar saldo do parceiro", error, error.response);
                setTimeout(() => {
                    this.buscarSaldoCaixa();
                }, 2000);
            })
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscarLancamentos();
        },

        buscarLancamentos(){
            let obj = {...this.filtroLancamentos};
            obj.data_inicial = moment(obj.data_inicial).format('YYYY-MM-DD');
            obj.data_final = moment(obj.data_final).format('YYYY-MM-DD');
            caixaService.listarParceiro(this.form.id, obj).then(
                response => {
                    this.lancamentos = response.data.data.map(x => {
                        x.dia = moment(x.data_lancamento).format("DD/MM/YYYY");
                        return x;
                    });
                    this.paginacaoLancamentos = response.data;
                    this.filtroLancamentos.page = response.data.current_page;
                    this.paginacaoLancamentos.per_page = response.data.per_page;
                }
            )
        },


        linkImprimirExtrato: function(){
            let obj = {...this.filtroLancamentos};
            obj.data_inicial = moment(obj.data_inicial).format('YYYY-MM-DD');
            obj.data_final = moment(obj.data_final).format('YYYY-MM-DD');
            return caixaService.link_extrato_parceiro(this.form.id, obj)
        },

        desabilitaDataInicial: function(data) {
            if(!this.filtroLancamentos.data_final)
                return false;
            return this.filtroLancamentos.data_final <= data
        },
        desabilitaDataFinal: function(data) {
            if(!this.filtroLancamentos.data_inicial)
                return false;
            return this.filtroLancamentos.data_inicial >= data
        },


        buscarComissoes: function(){
            this.sucessoComissoesSubGrupo = null;
            this.errosComissoesSubGrupos = null;
            parceiroService.buscarComissoes(this.form.id).then( response =>{
                this.subgrupos = response.data.map(x => {
                    x.pivot.comissao = Number(x.pivot.comissao)
                    return x
                })
            })
        },

        salvarComissoes: function(){
            this.sucessoComissoesSubGrupo = null;
            this.errosComissoesSubGrupos = null;
            parceiroService.atualizarComissoes(this.form.id, {subgrupos: this.subgrupos}).then( response =>{
                this.sucessoComissoesSubGrupo = "Comissões Atualizadas com Sucesso!";
            }).catch( error => this.errosComissoesSubGrupos = error);
        },

        buscarTabelaServicos: function(){
            this.sucessoTabelaServicos= null;
            this.errosTabelaServicos = null;
            parceiroService.buscarTabelaServicos(this.form.id).then( response =>{
                this.servicos = response.data.map(x => {
                    x.pivot.valor_unitario = Number(x.pivot.valor_unitario)
                    x.valor_unitario = Number(x.valor_unitario)
                    return x
                })
            })
        },

        salvarTabelaServicos: function(){
            this.sucessoTabelaServicos= null;
            this.errosTabelaServicos = null;
            parceiroService.atualizarTabelaServicos(this.form.id, {servicos: this.servicos}).then( response =>{
                this.sucessoTabelaServicos= "Tabela de Serviços Atualizadas com Sucesso!";
            }).catch( error => this.errosTabelaServicos = error);
        },


        imprimirTabela: function(){
            let obj = {parceiro_id: this.form.id};
            let url = servicoItemService.imprimirTabela(obj);
            window.open(url, '_blank')
        },

        

    },
    computed: {
        cc_parceiro(){
            return this.$store.state.conta_corrente_parceiro
        },
        percentual(){
            return {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: ' %',
                precision: 2,
                masked: false
            }
        },
        preco(){
            return {
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false                   

            }
        },
    },
};
</script>
